.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 0 60px;
    gap: 40px;
}

@media screen and (min-width: 768px) {
    .container {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
    }
}
